import { FC } from 'react';
import { Link } from 'react-router-dom';

import { IBreadProps } from '../../../types/common-type';
import s from './BreadCrumps.module.sass';

const BreadCrumps: FC<IBreadProps> = ({ links, text }) => (
	<nav>
		<ul className={s.list}>
			<li className={s.link}>
				<Link to='/board-games'>Main</Link>
				<span>&gt;</span>
			</li>
			{links &&
				links.map(link => {
					if (link.path) {
						return (
							<li className={s.link} key={link.path}>
								<Link to={link.path}>{link.name}</Link>
								<span>&gt;</span>
							</li>
						);
					} else {
						return (
							<li className={s.link} key={link.name}>
								<span>{link.name}</span>
							</li>
						);
					}
				})}
			{text && (
				<li className={s.link}>
					<span>{text}</span>
				</li>
			)}
		</ul>
	</nav>
);

export default BreadCrumps;
